export default {
	data() {
		return {
			title: 'get/setClipboardData',
			data: ''
		}
	},
	methods: {
		dataChange: function (e) {
			this.data = e.target.value
		},
		getClipboard: function () {
			uni.getClipboardData({
				success: (res) => {
					console.log(res.data);
					uni.showModal({
						title: '读取剪贴板',
						content: '剪贴板内容为: ' + res.data,
						showCancel: false
					})
				},
				fail: () => {
					uni.showModal({
						content: '读取剪贴板失败!',
						showCancel: false
					})
				}
			});
		},
		setClipboard: function () {
			var data = this.data;
			if (data.length === 0) {
				uni.showModal({
					title: '设置剪贴板失败',
					content: '内容不能为空',
					showCancel: false
				})
			} else {
				uni.setClipboardData({
					data: data,
					success: () => {
						uni.showModal({
							content: '设置剪贴板成功',
							showCancel: false
						})
					},
					fail: () => {
						uni.showModal({
							content: '储存数据失败!',
							showCancel: false
						})
					}
				});
			}
		}
	}
}
